import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home
  },
  {
    path: "/mywork",
    name: "MyWork",
    component: () =>
      import(/* webpackChunkName: "about" */ "../views/MyWork.vue")
  }
  ,
  {
    path: "/four",
    name: "Four",
    component: () =>
      import(/* webpackChunkName: "four" */ "../views/Four.vue")
  },
  {
    path: "/video",
    name: "Video",
    component: () =>
      import(/* webpackChunkName: "video" */ "../views/Video.vue")
  },
  {
    path: "/graphics",
    name: "Graphics",
    component: () =>
      import(/* webpackChunkName: "graphics" */ "../views/Graphics.vue")
  }
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes
});

export default router;
